@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'Agency FB';
  src: url("../fonts/AgencyFBBlack.ttf");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'Agency FB';
  src: url("../fonts/AgencyFBBold.ttf");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Agency FB';
  src: url("../fonts/AgencyFBLight.ttf");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Agency FB';
  src: url("../fonts/AgencyFBRegular.ttf");
  font-weight: 300;
  font-style: normal;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("../img/ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("../fonts/slick.eot");
  src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  margin-top: -10px;
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before, .slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

.slick-dots {
  position: absolute;
  bottom: 22px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.slick-dots li button {
  display: block;
  padding: 3px;
  background: none;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: red;
  cursor: pointer;
}

.slick-dots li button:before {
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #979795;
  transition: all 0.1s ease;
}

.slick-dots li button:hover:before {
  background: #4E4E4E;
}

.slick-dots li.slick-active button:before {
  background: #4E4E4E;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.block {
  width: 100%;
  background-color: #1E1D32;
  padding: 115px 0 50px 0;
}

.block__container {
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.block__list {
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  margin: 0 -15px;
}

.block__list--2items .block__list-item {
  max-width: 50%;
  flex: 50%;
}

.block__list-item {
  padding: 0 15px;
  max-width: calc(100% / 3);
  flex: calc(100% / 3);
}

.block__head {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 85px;
}

.block__title {
  text-shadow: 0 2px 4.8px rgba(0, 0, 0, 0.3);
  color: white;
  font-family: "Agency FB";
  font-size: 40px;
  font-weight: 700;
  line-height: 34px;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  white-space: nowrap;
}

.block__title:after {
  content: attr(data-backword);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.1;
  text-shadow: 0 2px 4.8px rgba(0, 0, 0, 0.3);
  color: #c2d4f8;
  font-family: "Agency FB";
  font-size: 88px;
  font-weight: 700;
  line-height: 88px;
  text-transform: uppercase;
  text-align: center;
  white-space: nowrap;
  pointer-events: none;
}

.block__subtitle {
  margin-top: 40px;
  color: #c2d4f8;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  text-align: center;
}

.block__content {
  width: 100%;
  position: relative;
}

.block__slider {
  position: relative;
  width: 100%;
}

.block__action {
  margin-top: 60px;
}

.gallery {
  display: flex;
  flex-direction: column;
}

.gallery__top {
  width: 100%;
  margin-bottom: 55px;
}

.gallery__top-slider {
  display: flex;
  justify-content: center;
  margin: 0 -30px;
}

.gallery__top-slider-item {
  padding: 0 30px;
  color: #FFF;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 700;
  line-height: 25px;
  text-transform: uppercase;
  transition: all 300ms;
}

.gallery__top-slider-item:hover {
  color: #3dd0d8;
  cursor: pointer;
}

.gallery__top-slider-item.active {
  color: #3dd0d8;
}

.gallery__bottom {
  width: 100%;
}

.gallery__bottom-slider {
  display: flex;
  margin: 0 -20px;
}

.gallery__bottom-slider-item {
  width: 100%;
  padding: 0 20px;
}

.hero {
  width: 100%;
  height: 100vh;
  min-height: 500px;
  display: flex;
  background-color: #151423;
}

.hero__container {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
}

.hero__slider {
  display: flex;
  width: 100%;
  height: 100%;
}

.hero .slick-list, .hero .slick-track {
  height: 100%;
}

.hero__item {
  width: 100%;
  height: 100%;
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.hero__bg {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover';
}

.hero__title {
  text-shadow: 0 2px 4.8px rgba(0, 0, 0, 0.3);
  color: white;
  font-family: "Agency FB";
  font-size: 48px;
  font-weight: 700;
  line-height: 54px;
  text-transform: uppercase;
  text-align: center;
}

.hero__title--big {
  font-size: 96px;
  line-height: 96px;
}

.hero__subtitle {
  margin: 40px 0 50px 0;
  text-shadow: 0 2px 4.8px rgba(0, 0, 0, 0.3);
  color: white;
  font-family: "Roboto";
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
}

.upcoming-games {
  display: flex;
  flex-direction: column;
}

.upcoming-games__top {
  width: 100%;
  margin-bottom: 85px;
  position: relative;
}

.upcoming-games__top-slider {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 0 -60px;
}

.upcoming-games__top-item {
  display: flex !important;
  justify-content: center;
  padding: 0 60px;
}

.upcoming-games__bottom {
  width: 100%;
  position: relative;
}

.upcoming-games__bottom-slider {
  display: flex;
  margin: 0 -60px;
}

.upcoming-games__bottom-item {
  width: 100%;
  padding: 0 60px;
}

.card-full {
  width: 100%;
