  border-radius: 10px;
  background-color: #292845;
}

.card-full__row {
  display: flex;
  align-items: stretch;
  margin: 0 -45px;
}

.card-full__col {
  padding: 0 45px;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 50%;
  position: relative;
}

.card-full__col:nth-child(1) {
  max-width: 560px;
  flex-grow: 1;
}

.card-full__box {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-full__tag {
  display: flex;
}

.card-full__tag-item {
  color: #3dd0d8;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
  margin-right: 24px;
