}

.card-post__date {
  color: #c2d4f8;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
}

.card-post__title {
  margin-top: 20px;
  margin-bottom: 30px;
  color: white;
  font-family: "Agency FB";
  font-size: 24px;
  font-weight: 700;
