.card-post__bottom-item-icon {
  width: 15px;
  height: 15px;
  min-width: 15px;
  margin-right: 10px;
}

.card-post__bottom-item-icon svg {
  fill: #3dd0d8;
  width: 15px;
  height: 15px;
}

.card-post__bottom-item-title {
  color: #c2d4f8;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 300;
  line-height: 14px;
