  margin-bottom: 30px;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  transition: all 300ms;
}

.card-simple__img-container:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(61, 208, 216, 0.5);
  background-image: linear-gradient(to right, rgba(61, 208, 216, 0.64) 0%, #9252EA 100%);
  opacity: 0;
  transition: all 300ms;
}

.card-simple__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover';
}

.card-simple__title {
  color: white;
  font-family: "Agency FB";
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  text-transform: uppercase;
}

.card-simple__subtitle {
  color: #c2d4f8;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  min-width: 220px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  background-color: transparent;
  text-align: center;
  color: white;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 700;
  line-height: 60px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #3dd0d8;
  transition: all 300ms;
}

.btn:hover {
  cursor: pointer;
}

.btn:hover:before {
  opacity: 0;
}

.btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(61, 208, 216, 0.64) 0%, #9252EA 100%);
  opacity: 1;
  z-index: 0;
  transition: all 300ms;
}

.btn--transparent {
  background-color: transparent;
  box-shadow: inset 0 0 0 2px #FFF;
}

.btn--transparent:hover {
  color: #FFF;
  background-color: #3dd0d8;
  box-shadow: inset 0 0 0 2px #3dd0d8;
}

.btn--transparent:before {
  content: none;
}

.btn span {
  z-index: 1;
}

.gradient {
  background-color: #3dd0d8;
  transition: all 300ms;
}

.gradient:hover {
  cursor: pointer;
}

.gradient:hover:before {
  opacity: 0;
}

.gradient:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(61, 208, 216, 0.64) 0%, #9252EA 100%);
  opacity: 1;
  z-index: 0;
  transition: all 300ms;
}

.hero-slider-controls {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100px;
  width: 40px;
  margin: auto 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.hero-slider-controls__item-container.slick-current .hero-slider-controls__item {
  opacity: 1;
  transform: scale(2) rotate(90deg);
  position: relative;
  pointer-events: none;
}

.hero-slider-controls__item-container.slick-current .hero-slider-controls__item:after {
  cursor: default;
  content: "";
  position: absolute;
  right: -50px;
  top: 0;
}

.hero-slider-controls__item {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: "Agency FB";
  font-size: 24px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
  transform: scale(1) rotate(90deg);
  opacity: 0.8;
  transition: all 300ms;
}

.hero-slider-controls__item:hover {
  cursor: pointer;
  opacity: 1;
}

.hero-slider-controls__item.active {
  opacity: 1;
  transform: scale(2) rotate(90deg);
  position: relative;
  pointer-events: none;
}

.hero-slider-controls__item.active:after {
  cursor: default;
  content: "";
  position: absolute;
  right: -50px;
  top: 0;
}

.hero-socials {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 100px;
  margin: auto 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.hero-socials__item {
  width: 40px;
  height: 40px;
  background-color: white;
  margin-bottom: 30px;
  border-radius: 50%;
  padding: 12px;
  transition: all 300ms;
}

.hero-socials__item svg {
  width: 100%;
  height: 100%;
  fill: #1e1d32;
  transition: all 300ms;
}

.hero-socials__item:last-child {
  margin-bottom: 0;
}

.hero-socials__item:hover svg {
  fill: #3dd0d8;
}

.rating {
  display: flex;
  align-items: center;
}

.rating__item {
  width: 14px;
  min-width: 14px;
  height: 14px;
  margin-right: 3px;
}

.rating__item:last-child {
  margin-right: 0;
}

.rating__item svg {
  width: 14px;
  height: 14px;
  fill: #f9a450;
}

.slider-arrow {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  width: 10px;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  opacity: 0.9;
  transition: all 300ms;
}

.slider-arrow:last-child {
  margin-right: 0;
}

.slider-arrow:after, .slider-arrow:before {
  pointer-events: none;
  content: "";
  position: absolute;
  width: 2px;
  height: 10px;
  background-color: #3dd0d8;
  border-radius: 5px;
  transition: all 300ms;
}

.slider-arrow:after {
  top: 6px;
  transform: rotate(-45deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
}

.slider-arrow:before {
  bottom: 6px;
  transform: rotate(45deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
}

.slider-arrow:hover {
  cursor: pointer;
  opacity: 1;
}

.slider-arrow:hover:after, .slider-arrow:hover:before {
  border-color: #3dd0d8;
}

.slider-arrow--prev {
  left: -55px;
}

.slider-arrow--next {
  right: -55px;
  transform: rotate(180deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
}

.slider-arrow.slick-disabled {
  opacity: 0.5;
  pointer-events: none;
  transition: all 300ms;
}

.slider-dots {
  margin-top: 80px;
  height: 14px;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}

.slider-dots ul {
  width: auto !important;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

.slider-dots li {
  box-sizing: border-box;
  width: 14px;
  min-width: 14px;
  height: 14px;
  box-shadow: inset 0 0 0px 1.5px #fff;
  border-radius: 50%;
  background-color: transparent;
  margin-right: 14px;
  transition: all 300ms;
}

.slider-dots li:last-child {
  margin-right: 0;
}

.slider-dots li.slick-active {
  transition: all 300ms;
  background-color: #FFF;
}

.slider-dots button {
  display: none !important;
}

.slider-dots button:before {
  content: none;
}

.table-grid__row {
  width: 100%;
  display: flex;
}

.table-grid__col {
  width: 100%;
  min-height: 300px;
  position: relative;
}

.table-grid__col--small {
  max-width: 300px;
}

.table-grid__item {
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
  background-color: #181728;
  display: flex;
  justify-content: center;
  align-items: center;
}

.table-grid__item:hover:after {
  opacity: 0.7;
}

.table-grid__item:hover .table-grid__overlay {
  opacity: 1;
}

.table-grid__item:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFF;
  background-image: linear-gradient(to right, rgba(61, 208, 216, 0.64) 0%, #9252EA 100%);
  opacity: 0;
  z-index: 0;
  transition: all 300ms;
}

.table-grid__item--large {
  height: 100%;
}

.table-grid__overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  z-index: 1;
  transition: all 300ms;
}

.table-grid__overlay-title {
  max-width: 200px;
  margin-bottom: 30px;
  color: white;
  font-family: "Agency FB";
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
}

.table-grid__overlay-title:hover {
  color: #1e1d32;
  cursor: pointer;
}

.table-grid__overlay-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.table-grid__overlay-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  box-shadow: inset 0 0 0 1px #FFF;
  border-radius: 10px;
  overflow: hidden;
  margin-right: 10px;
  text-decoration: none;
  transition: all 300ms;
}

.table-grid__overlay-item:hover {
  box-shadow: inset 0 0 0 1px #3dd0d8;
  background-color: #3dd0d8;
}

.table-grid__overlay-item:last-child {
  margin-right: 0;
}

.table-grid__overlay-icon {
  width: 16px;
  height: 16px;
}

.table-grid__overlay-icon svg {
  width: 16px;
  height: 16px;
  fill: #fff;
}

.btnTO {
  position: fixed;
  right: 15px;
  bottom: 15px;
  transition: none !important;
}
