.hero-socials {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 100px;
	margin: auto 0;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;

	&__item {
		width: 40px;
		height: 40px;
		background-color: white;
		margin-bottom: 30px;
		border-radius: 50%;
		padding: 12px;
		svg {
			width: 100%;
			height: 100%;
			fill: #1e1d32;
			@include tr; }

		@include tr;
		&:last-child {
			margin-bottom: 0; }
		@include h {
			svg {
				fill: $accent; } } } }
