.slider-arrow {
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto 0;
	width: 10px;
	height: 16px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 2;
	opacity: 0.9;
	@include tr;
	&:last-child {
		margin-right: 0; }
	&:after, &:before {
		pointer-events: none;
		content: "";
		position: absolute;
		width: 2px;
		height: 10px;
		background-color: $accent;
		border-radius: 5px;
		@include tr; }
	&:after {
		top: 6px;
		@include rotate(-45); }
	&:before {
		bottom: 6px;
		@include rotate(45); }
	@include h {
		cursor: pointer;
		opacity: 1;
		&:after, &:before {
			border-color: $accent; } }

	&--prev {
		left: -55px; }

	&--next {
		right: -55px;
		@include rotate(180); }

	&.slick-disabled {
		opacity: 0.5;
		pointer-events: none;
		@include tr; } }

