.hero-slider-controls {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 100px;
	width: 40px;
	margin: auto 0;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;

	&__item-container {
		&.slick-current {
			.hero-slider-controls__item {
				opacity: 1;
				transform: scale(2) rotate(90deg);
				position: relative;
				pointer-events: none;
				&:after {
					cursor: default;
					content: "";
					position: absolute;
					right: -50px;
					top: 0; } } } }

	&__item {
		height: 40px;
		width: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		color: white;
		font-family: $fa;
		font-size: 24px;
		font-weight: 700;
		line-height: 20px;
		text-transform: uppercase;
		transform: scale(1) rotate(90deg);
		opacity: 0.8;
		@include tr;
		@include h {
			cursor: pointer;
			opacity: 1; }
		&.active {
			opacity: 1;
			transform: scale(2) rotate(90deg);
			position: relative;
			pointer-events: none;
			&:after {
				cursor: default;
				content: "";
				position: absolute;
				right: -50px;
				top: 0; } } } }
